
.register-form {
 margin-top: 40px;
 width: 550px;
}

.main {
 margin-top: 70px;
 display: -webkit-flex;
 display: flex;
 -webkit-justify-content: center;
         justify-content: center;
}


@media all and (min-width: 480px) {
    .Login {
      padding: 60px 0;
      width: 300px;
    }

    .Login Form {
      margin: 0 auto;
      max-width: 320px;
    }
  }

.login-form-button {
   width: 100%;
}

.logout {
 padding-top: 20px;
 padding-right: 20px;
 float: right;
}


.App {
 text-align: center;
}

.App-logo {
 -webkit-animation: App-logo-spin infinite 20s linear;
         animation: App-logo-spin infinite 20s linear;
 height: 60px;
 float: left;
}

.App-header {
 background-color: #222;
 height: 60px;
 color: white;
}

.App-title {
 font-size: 1.5em;
 float: left;
 color: #61dafb;
 padding-top: 16px;
}

.Home-title {
 font-size: 0.8em;
 float: right;
 padding-top: 3px;
 padding-left: 6px;
}

a {
  color: #CCE3EC;
}

.App-intro {
 font-size: large;
}

@-webkit-keyframes App-logo-spin {
 from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
 to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
 from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
 to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



