@import '~antd/dist/antd.css';
@import 'Register.css';
@import 'Main.css';
@import 'Login.css';
@import 'Header.css';
@import 'Upload.css';

.App {
 text-align: center;
}

.App-logo {
 animation: App-logo-spin infinite 20s linear;
 height: 60px;
 float: left;
}

.App-header {
 background-color: #222;
 height: 60px;
 color: white;
}

.App-title {
 font-size: 1.5em;
 float: left;
 color: #61dafb;
 padding-top: 16px;
}

.Home-title {
 font-size: 0.8em;
 float: right;
 padding-top: 3px;
 padding-left: 6px;
}

a {
  color: #CCE3EC;
}

.App-intro {
 font-size: large;
}

@keyframes App-logo-spin {
 from { transform: rotate(0deg); }
 to { transform: rotate(360deg); }
}


